import React, {} from 'react'; 
import '../css/home-search-bar.css'; 

// ICONS
import { IconContext } from "react-icons";
import { FiSearch } from 'react-icons/fi';
import { FaBlackberry } from 'react-icons/fa';


const SearchBar = () => {



  return       ( 

    <div className="search-bar-container">


    <div className="the-search-bar-and-icon">

        <div className="search-bar-active-wrapper"></div>

        <span className="search-bar-icon">

        <IconContext.Provider value={{color: 'black', size: '1.2em' }}>

    <FiSearch/>

</IconContext.Provider>


            {/* end of .search-bar-icon */}
        </span>


    <input type="text" placeholder='Search transactions...' maxlength='128' />






        {/* end of  .the-search-bar-and-icon*/}
    </div>


    <button className="search-button">

        <div className="search-button-wrapper"></div>

            Search


            {/* end of .search-button */}
    </button>



        {/* end of .search-bar-container */}
    </div>
    

)
};

export default SearchBar;