import React, {} from 'react'; 
import '../css/transaction-details.css';
import {useGlobal} from '../context'; 

// ICONS
import { IconContext } from "react-icons";
import { BiMoney } from 'react-icons/bi';
import { GiMilkCarton } from 'react-icons/gi';
import { FaShapes } from 'react-icons/fa';
import { HiOutlineShoppingCart, HiUsers } from 'react-icons/hi';
import { FiBook } from 'react-icons/fi';




const TransactionDetails = () => {

    const {userNameData, currentBalanceSee} = useGlobal(); 
    const {transactions} = userNameData;


    function imageConvert(x){
        if(x === 'income'){
            return <BiMoney />
        }

        if(x === 'shopping'){
            return <GiMilkCarton />
        }

        if(x === 'other person bank transfer'){
            return <HiUsers />
        }

        if(x === 'online shopping'){
            return <HiOutlineShoppingCart />
        }

        if(x === 'bill'){
            return <FiBook />
        }

        return <FaShapes />


        // end of function imageConvert
    }

    function convertEuro(x){
        return new Intl.NumberFormat('eu-EU', { style: 'currency', currency: 'EUR' }).format(x);
    }


  return       (

    <div className="transactions-container">
    

  {transactions.map(value=> {
      const {name, date, amount, type} = value;
      return (
          <div key={date+name} className="single-transaction-holder-outer">

              <div className="single-transaction-outer">

              <div className="transaction-type">

              <IconContext.Provider value={{ size: '1.5em', color: 'black' }}>
                    <div className="icon-holder">
                             {imageConvert(type)}
                    </div>
                    

                    </IconContext.Provider>

                  
                  </div>

              <div className='amount-box'>

                  <div className={
                      amount > 0 && currentBalanceSee ? "amount-holder" 
                      : amount > 0 && !currentBalanceSee ? 'amount-holder-blur' : amount < 0 && currentBalanceSee ?"amount-holder-negative" : 'amount-holder-negative-blur'}>
                        {convertEuro(amount)}
                  </div>
                  
                  
                  
                  {/* end of .amount-box */}
                  </div>

                <div className="name-date-holder">

                <div className="name">{name}</div>
                <div className="date">{date.replace(/-/g, ' ')}</div>

                {/* end of .name-date-holder */}
                </div>




                {/* end of .single-transaction-outer */}
              </div>


           


              {/* end of .single-transaction-holder-outer */}
          </div>
      )
  })}




  {/* end of .transactions-container */}
</div>
)
};

export default TransactionDetails;