import React, {useState, useEffect} from 'react';
import '../css/login.css';
import Footer from './Footer';
import {AiFillEye, AiFillEyeInvisible} from 'react-icons/ai';
import {RiErrorWarningFill} from 'react-icons/ri';
import { IconContext } from "react-icons";
import {Link} from 'react-router-dom';
import axios from 'axios';


// OTHER COMPONENTS
import UserBankAccountPage from './UserBankAccountPage';


const Login = () => {

 
  const [forgot, setForgot] = useState(true);
  const [loginPassInput, setLoginPassInput] = useState('');
  const [loginEmailInput, setLoginEmailInput] = useState('');
  const [seePass, setSeePass] = useState('password');
  const [loginValid, setLoginValid] = useState('valid');
  const [selectedInputBox, setSelectedInputBox] = useState('');
  


  const seePassword = () => {

    if(seePass === 'password'){
      setSeePass('text')
    } else {
      setSeePass('password')
    }

  }


  const handleSubmit = (e) => {
    e.preventDefault(); 
  }
  


  const checkLogin = async() => {


    if(loginEmailInput === ''){
      document.querySelector('#email').focus(); 
      return; 
    }

    if(loginPassInput === ''){
      document.querySelector('#password').focus(); 
      return; 
    }
     

      const res = await axios.post('https://n26-clone-server.herokuapp.com/login-data', {email: loginEmailInput, password: loginPassInput});

      const data = await res.data;
      console.log(data);

      if(data === 'not valid'){

        console.log('not valid');

        setLoginValid('not valid');

        setForgot(true);

      } else {  
        localStorage.setItem('userDetails', JSON.stringify(data))
        window.location.href = '/user-bank-account-page';
      }

      // END OF CHECK LOGIN...
  }





  useEffect(()=> {
    if(loginPassInput.length > 0){
      setForgot(false)
    } else {
      setForgot(true)
    }
  }, [loginPassInput]);


  useEffect(()=> {

    let selectInputBox = document.querySelectorAll('#container-login #login-form .inputBox input');

 
    if(loginValid === 'not valid'){
      
      selectInputBox.forEach((value, index)=> {
        value.style.border = '1px solid #B44B46';
        value.addEventListener('focus', ()=> setSelectedInputBox(selectInputBox[index]));
        
      });
      
          
      // end of valid
    }

  }, [loginValid]);


  useEffect(()=> {

    if(loginValid === 'not valid'){

      let selectInputBox = document.querySelectorAll('#container-login #login-form .inputBox input');

   

      selectInputBox.forEach(value=> value.style.border = '1px solid #B44B46');
      // selectedInputBox.style.border = '4px solid #B44B46';
  
      console.log(selectedInputBox);
  
      if(selectedInputBox){
        console.log('can be styled');
        selectedInputBox.style.border = '4px solid #B44B46';
      }
  


      // end of IF statement
    }

  }, [selectedInputBox])



  useEffect(()=> {
    console.log(loginValid)
  }, [])


  


  return       ( <>
    {/* div soup */}

<div id="container-login">


<form id='login-form' onSubmit={e=> handleSubmit(e)}>

    <h1>N26</h1>
    
    <div className="inputBox">

    <input type='text' required name='email' id='email' value={loginEmailInput} onChange={e=> setLoginEmailInput(e.target.value)}/>
    <span>Email</span>

    </div>
    
    <div className="inputBox">
    <input type={seePass} id='loginPassword' value={loginPassInput} required onChange={e=> setLoginPassInput(e.target.value)} name='password' id='password' />
    <span>Password</span>

    {forgot ? <Link to='/forgot-password' id='forgot'>Forgot?</Link> : 
    
    <button type='button' id='eye' onClick={()=> seePassword()}>
      
    <IconContext.Provider value={{ size: '1.75em'}}>
      <div>
      {seePass === 'password' ? <AiFillEye /> : <AiFillEyeInvisible />}
      </div>
      </IconContext.Provider>
      
      </button>}

    </div>

 
    {loginValid === 'not valid' && <div className='not-valid-text inputBox'>
     <div className='login-warning-icon'><RiErrorWarningFill /></div> Error! The email address or password is incorrect</div>
      }
  
    <button type='submit' id='loginButton' onClick={()=> checkLogin()}>Log in</button>

   
    <Link to='/new-account'>Create an account</Link> 
     

</form>

<Footer />


{/* end of container */}
  </div>


      {/* div soup */}
      </>
)
};

export default Login;