import React, {useState, useEffect} from 'react'; 
import '../css/practice.css';
import SpinningLoadingThingy from './SpinningLoadingThingy';


// ICONS
import { IconContext } from "react-icons";
import { AiOutlineCreditCard, AiOutlineUser, AiOutlineClose } from 'react-icons/ai';

// MODULES
const moment = require('moment');


const PracticePage = () => {

    const [stretch, setStretch] = useState(false);



  function dropdownClick(){

  document.querySelector('.dropdown-box').classList.add('dropdown-box-stretched');
  


    // end of dropdownClick()
  }

  function optionClick(e){

    e.stopPropagation(); 

    document.querySelector('.dropdown-box').classList.remove('dropdown-box-stretched');


    // end of optionClick(); 
  }
  

 

  return       ( <>
    {/* div soup */}

<div className="container-practice">


  <div className="dropdown-box" onClick={()=> dropdownClick()}>

    <div className="option" onClick={(e)=> optionClick(e)}>option1</div>
    <div className="option" onClick={(e)=> optionClick(e)}>option2</div>
    <div className="option" onClick={(e)=> optionClick(e)}>option3</div>



    {/* end of .dropdown-box */}
  </div>


 

  {/* end of .container-practice */}
</div>

      {/* div soup */}
      </>
)
};

export default PracticePage;