import React, {} from 'react'; 
import { GoLinkExternal } from 'react-icons/go';
import {Link} from 'react-router-dom';

const Footer = () => {


  return       ( <>
    {/* div soup */}

    <footer>

<div id="footerDiv">
  
<span id="logo">&copy; N26 GmbH 2021</span>
<Link to='/privacy-policy' id="privacy" className='link focusedCode'>Privacy Policy <GoLinkExternal /></Link>
<Link to='/imprint-page' id="imprint" className='link focusedCode'>Imprint <GoLinkExternal /></Link>


<select name="language" id="language" className='focusedCode'>
  <option selected value>English</option>
  <option>Deutsch</option>
  <option>Francais</option>
  <option>Espanol</option>
  <option>Italiano</option>
</select>


</div>
</footer>


      {/* div soup */}
      </>
)
};

export default Footer;