import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import '../css/account-page-header.css';
import {useGlobal} from '../context';

// ICONS
import {TiThMenu, TiTabsOutline} from 'react-icons/ti';
import {FaCubes} from 'react-icons/fa';
import {FiCheckSquare} from 'react-icons/fi';
import {BiUser} from 'react-icons/bi';

// COMPONENT IMPORTS
import CurrentBalance from './CurrentBalance'; 


const UserBankAccountHeader = () => {


    const {scrollSize, 
      selectedHeaderValue, setSelectedHeaderValue
    } = useGlobal();



    function handleMenuItemClick(x){

      // console.log(x);
      setSelectedHeaderValue(x);
      localStorage.setItem('selectedItem', x)
           
    };


    const resetHeaderNavBarStyles = () => {

      let color1 = 'black';

      let home = document.querySelector('#home');
      let spaces = document.querySelector('#spaces');
      let exploreN26 = document.querySelector('#exploreN26');
      let actions = document.querySelector('#actions');
      let myAccount = document.querySelector('#myAccount');

      if(home){

        // console.log('home is true')

        home.style.color = color1;
        spaces.style.color = color1;
        exploreN26.style.color = color1;
        actions.style.color = color1;
        myAccount.style.color = color1;
      }


      // end of resetHeaderNavBarStyles()
    }

    



    useEffect(()=> {

      resetHeaderNavBarStyles();
      

      let selectedItem = localStorage.getItem('selectedItem');
      // console.log(selectedItem);

      let selectedElement = document.querySelector(`#${selectedItem}`);
      // console.log(selectedElement);

      if(selectedElement){
        selectedElement.style.cssText = `
        color: #48AC98;
        `
      }



    }, [selectedHeaderValue])


  useEffect(()=> {

    let header1 = document.querySelector('#account-page-header-container header');

    if(header1){

     if(scrollSize > 0){
       header1.style.cssText = `
        background-color: #ffffff;
        box-shadow: 3px 0px 4px 3px #EBEBEB;
       `
     } else {
      header1.style.cssText = `
      background-color: #f9f9f9;
      box-shadow: none;
     `
     }


    }
  }, [scrollSize])



  // FUNCTIONS


  // HTML 

  return       ( 

  <div id="account-page-header-container">

    {/* THIS IS HEADER CODE  */}

    <header>

      {scrollSize > 0 &&<div className="hidden-current-balance-container">
        <div className="current-balance-holder">
            <CurrentBalance />
        </div>
          
      </div>}


    <div className="menu-items-holder">

  
{/* ----------------------------------------------------------------------- */}
      <Link className="menu-item-box" to='/user-bank-account-page' id='home' onClick={()=> handleMenuItemClick('home')}>

        <span className="icon-holder">
          <TiThMenu />
        </span>

        <span className="icon-label">
         Home
        </span>

          {/* end of .menu-item-box */}
      </Link>


{/* ----------------------------------------------------------------------- */}

      <Link className="menu-item-box" to='/spaces' id='spaces' onClick={()=> handleMenuItemClick('spaces')}>

<span className="icon-holder">
  <FaCubes />
</span>

<span className="icon-label">
 Spaces
</span>

  {/* end of .menu-item-box */}
</Link>

{/* ----------------------------------------------------------------------- */}

<Link className="menu-item-box" to='/exploreN26' id='exploreN26' onClick={()=> handleMenuItemClick('exploreN26')}>

<span className="icon-holder">
  <TiTabsOutline />
</span>

<span className="icon-label">
 Explore N26
</span>

  {/* end of .menu-item-box */}
</Link>


{/* ----------------------------------------------------------------------- */}

<Link className="menu-item-box" to='/actions' id='actions' onClick={()=> handleMenuItemClick('actions')}>

<span className="icon-holder">
  <FiCheckSquare />
</span>

<span className="icon-label">
 Actions
</span>

  {/* end of .menu-item-box */}
</Link>


{/* ----------------------------------------------------------------------- */}

<Link className="menu-item-box" to='/my-account' id='myAccount' onClick={()=> handleMenuItemClick('myAccount')}>

<span className="icon-holder">
  <BiUser />
</span>

<span className="icon-label">
 My Account
</span>

  {/* end of .menu-item-box */}
</Link>

  


  {/* end of menu=items-holder */}
  </div>

<div className="logo-holder">
  
<Link to='/user-bank-account-page'><h2>N26</h2></Link>

{/* end of logo-holder */}
</div>

      {/* end of header */}
      </header>

      {/* THIS IS END OF HEADER TEXT */}


      {/* end of ACCOUNT-PAGE-CONTAINER */}
    </div>


)
};

export default UserBankAccountHeader;