import React, {useState, useEffect, useContext} from 'react';

const AppContext = React.createContext();

export const AppProvider = ({children}) => {

    let userNameData = JSON.parse(localStorage.getItem('userDetails'));

    const [scrollSize, setScrollSize] = useState(0);
    const [selectedHeaderValue, setSelectedHeaderValue] = useState('');
    const [currentBalanceSee, setCurrentBalanceSee] = useState(true);
    const [addMoneyMethodVisible, setAddMoneyMethodVisible] = useState(false);

    // top-up page
    const [inputValue, setInputValue] = useState(50);
    const [minimumPopUp, setMinimumPopUp] = useState(false);
    const [showLoadingSpin, setShowLoadingSpin] = useState(false);



      

    return (
        <AppContext.Provider value={{
            scrollSize, setScrollSize,
            selectedHeaderValue, setSelectedHeaderValue,
            userNameData,
            currentBalanceSee, setCurrentBalanceSee,
            addMoneyMethodVisible, setAddMoneyMethodVisible,
            inputValue, setInputValue,
            minimumPopUp, setMinimumPopUp,
            showLoadingSpin, setShowLoadingSpin

        }}>
            {children}
        </AppContext.Provider>
    )
};

export const useGlobal = () => {
    return useContext(AppContext);
}