import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import '../css/user-account-page-body.css'; 
import {useGlobal} from '../context';


// COMPONENTS IMPORTS
import UserBankAccountHeader from './UserBankAccountHeader';
import Home5ButtonsContainer from './UserBankAccountHome5buttons';
import CurrentBalance from './CurrentBalance';
import SearchBar from './HomeSearchBar.js';
import TransactionDetails from './TransactionDetails';



const UserBankAccountPage = () => {


  // CONTEXT IMPORTS
  const {setScrollSize, setSelectedHeaderValue} = useGlobal();




  // FUNCTIONS AND OTHER
  window.addEventListener('scroll', ()=> setScrollSize(window.scrollY));



  useEffect(()=> {
    setSelectedHeaderValue('home');
    localStorage.setItem('selectedItem', 'home');
  }, [])


  // HTML 

  return       ( 

  <div id="account-page-main-body">

    <UserBankAccountHeader />


    <div id="balance-and-search-main-container">

    <Home5ButtonsContainer />

    <CurrentBalance />

    <SearchBar />


      {/* end #balance-and-search */}
    </div>

    <div id="user-account-transactions-container">
       <TransactionDetails />
    </div>





    {/* end of #account-page-main-body */}
  </div>







)
};

export default UserBankAccountPage;