import React, {useEffect, useState} from 'react'; 
import UserBankAccountHeader from './UserBankAccountHeader';
import {useGlobal} from '../context';
import '../css/top-up.css'; 

// COMPONENTS
import SpinningLoadingThingy from './SpinningLoadingThingy';


// ICONS
import { IconContext } from "react-icons";
import { AiOutlineInfoCircle, AiFillInfoCircle } from 'react-icons/ai';
import { GoLinkExternal } from 'react-icons/go';


// MODULE IMPORTS
const axios = require('axios').default;


const TopUp = () => {


  const {
    setAddMoneyMethodVisible, setSelectedHeaderValue,
    inputValue, setInputValue,
    minimumPopUp, setMinimumPopUp,
    showLoadingSpin, setShowLoadingSpin
  
  } = useGlobal(); 


    //////////////////////////////////////////////////////////////////////////
  //.............               FUNCTIONS             .............
  /////////////////////////////////////////////////////////////////////////// 

 

  function topUpRedirect(){
    let newLocation = window.location.href + '/details';
    // console.log(newLocation);

    localStorage.setItem('amountEntered', inputValue); 

    setShowLoadingSpin(true);

    window.location.href = newLocation; 

    // setTimeout(()=> {
      
    // }, 2000)


    // end of topUpRedirect 
  }



    //////////////////////////////////////////////////////////////////////////
  //.............              USE EFFECTS            .............
  /////////////////////////////////////////////////////////////////////////// 

 

  useEffect(()=> {
    setAddMoneyMethodVisible(false);
    setSelectedHeaderValue('actions');
    localStorage.setItem('selectedItem', 'actions');
  }, [])


  useEffect(()=> {

    let euroSign = document.querySelector('.euro-sign');
    let topUpButton = document.querySelector('#top-up-button');

    if(inputValue.length === 0){

      setMinimumPopUp(true);
      topUpButton.disabled = true;
      topUpButton.style.backgroundColor = '#A0D2C8'; 
      topUpButton.style.cursor = 'auto'; 
      topUpButton.classList.remove('button-class-with-hover-shadow');
      
    

      if(euroSign){
        euroSign.style.color = 'lightgray';
      }
      
    }

    if(inputValue.length !== 0){

      // console.log('this should fire now')

      setMinimumPopUp(false);
      topUpButton.disabled = false;
      topUpButton.style.backgroundColor = '#36A18B'; 
      topUpButton.style.cursor = 'pointer'; 
      topUpButton.classList.add('button-class-with-hover-shadow');

      if(euroSign){
        euroSign.style.color = 'black';
      }
    }

  }, [inputValue])





    //////////////////////////////////////////////////////////////////////////
  //.............               HTML           .............
  /////////////////////////////////////////////////////////////////////////// 



  return       (     

    <div className="top-up-main-container-full-page">

    <UserBankAccountHeader />

    <div className="outer-box">


    <div className="text-icon-box-row1">

      <div className="text-heading">
        How much money would you like to add?

        {/* end of .text-heading */}
      </div>


      <div className="important-icon" onClick={()=> alert('will develop later')}>


          <IconContext.Provider value={{ color: "black", size: '1.7em' }}>

            <AiOutlineInfoCircle />

          </IconContext.Provider>

          {/* end of .important-icon */}
      </div>


    {/* end of .text-icon-box-row1 */}
    </div>

      {/* --------------------------------------------------------- */}

    
    <div className="amount-required-box">

      <div className="amount-required-text-line1">

      Amount () (Required)
        {/* end of .amount-required-text-line1 */}
      </div>


      <div className="input-box-and-euro-sign">

        <div className="input-box-and-euro-sign-inner-box">


        <div className="euro-sign">
        
          {/* end of .euro-sign */}
        </div>

        <input type="number" className='input-box' placeholder='50' value={inputValue} onChange={e => setInputValue(e.target.value)} />

          
          {/* end of .input-box-and-euro-sign-inner-box */}
        </div>

        
        {/* end of .input-box-and-euro-sign */}
      </div>

    {minimumPopUp && 
    
    <div className="pop-up-box">
    

          <IconContext.Provider value={{ color: "#B44B46", className: "pop-up-icon", size: '1.1em' }}>
            
              <AiFillInfoCircle />
                
          </IconContext.Provider>


          <div className="pop-up-box-text-box">

          Please enter an amount in 


          </div>

            {/* end of .pop-up-box */}
              </div>}


        <div className="we-charge-no-fees-box">

          <div className="we-charge-no-fees-text">
          We charge no fee for your first top-up!

            {/* end of .we-charge-no-fees-text */}
          </div>


          <div className="link-box" onClick={()=> alert('will develop later')}>

            <div className="text">
              Learn more
            </div>

            <div className="icon">

           

              <IconContext.Provider value={{ size: '1.1em'}}>
             
                  <GoLinkExternal />
             
              </IconContext.Provider>

            </div>

            
          {/* end of .link-box */}
          </div>



          {/* end .we-charge-no-fees-box */}
        </div>


        <div className="continue-button-box">

        <div className="continue-button-holder">
          
          <button id='top-up-button' onClick={()=> topUpRedirect()}>

            {showLoadingSpin && 
                        <div className="spin-loading-holder">
                        <SpinningLoadingThingy />
                        </div>
                        }

          
            Continue
            </button>
        </div>


          {/* end of .continue-button */}
        </div>



      {/* end of .amount-required-box */}
    </div>



      {/* end of .outer-box */}
    </div>
      

      {/* end .top-up-main-container-full-page */}
    </div>


)
};

export default TopUp;



