import React, {useState, useEffect} from 'react'; 
import '../css/top-up-details.css';
import {useGlobal} from '../context';


// COMPONENTS
import UserBankAccountHeader from './UserBankAccountHeader';
import SpinningLoadingThingy from './SpinningLoadingThingy';


// ICONS
import { RiErrorWarningLine } from 'react-icons/ri';
import {FiChevronDown} from 'react-icons/fi';


// MODULE IMPORTS
const axios = require('axios').default;
const moment = require('moment'); 



const TopUpDetails = () => {

  const {
    showLoadingSpin, setShowLoadingSpin,
    inputValue
  
  } = useGlobal(); 

  let amountEntered = parseFloat(localStorage.getItem('amountEntered'));
  console.log(amountEntered);


 
 
 
  //////////////////////////////////////////////////////////////////////////
  //.............                STATE MANAGEMENT                .............
  ///////////////////////////////////////////////////////////////////////////

  const [name, setName] = useState('');
  const [nameVisible, setNameVisible] = useState(false);
  const [type, setType] = useState('');
  const [typeVisible, setTypeVisible] = useState(false); 
  const [submitSuccess, setSubmitSuccess] = useState(false); 


    //////////////////////////////////////////////////////////////////////////
  //.............               USE EFFECTS         .............
  /////////////////////////////////////////////////////////////////////////// 


  //////////////////////////////////////////////////////////////////////////
  //.............               FUNCTIONS             .............
  /////////////////////////////////////////////////////////////////////////// 
  


  const handleSubmit = async (e) => {

    e.preventDefault(); 



 

    if(name.length === 0){
      return setNameVisible(true)
    } else {
      setNameVisible(false)
    }
    
    if(document.querySelector('#option1').innerHTML === 'Choose type'){
      return setTypeVisible(true)
    } 

    setShowLoadingSpin(true);


    // DATA SUBMISSION HAPPENS HERE

    let resp = await axios.post('https://n26-clone-server.herokuapp.com/postMDB', {name: name, date: moment().format('DD-MMM-YYYY'), amount: amountEntered, type: type});
    let data = await resp.data;
    console.log('data:', data);
    localStorage.setItem('userDetails', JSON.stringify(data));
    


    // PAGE REDIRECT HAPPENS HERE

    setTimeout(()=> {
      setSubmitSuccess(true);
      let pageLocation = window.location.origin + '/user-bank-account-page';
      window.location = pageLocation; 
    }, 1000)     


    // end of handleSubmit()
  }


 const dropdownMenu = () => {

    document.querySelector('#option1').innerHTML = 'Choose type'; 
    document.querySelector('#option1').classList.remove('black-color'); 

    document.querySelector('#dropdown').classList.add('dropdown-stretch');
    setTypeVisible(false);

    // end of  dropdownMenu() 
  }


  const optionSelect = (x, e) => {

    setType(x);

    e.stopPropagation(); 

    const option1 = document.querySelector('#option1');
    const dropDown = document.querySelector('#dropdown');

    option1.innerHTML = x[0].toUpperCase() + x.slice(1); 
    dropDown.classList.remove('dropdown-stretch');
    option1.classList.add('black-color');

    // end of optionSelect()
  }


  function inputValidator(e){
    setName(e.target.value);
    setNameVisible(false);
    // end of inputValidator(); 
  }




  //////////////////////////////////////////////////////////////////////////
  //.............                HTML CODE                     .............
  ///////////////////////////////////////////////////////////////////////////


  if(submitSuccess){
    return (
      <div className="top-up-details-main-container-full-page">
        <UserBankAccountHeader />

        <div className="top-up-details-container">
              <div className="title2">

Submitted successfully, returning to home page! 
{/* end of .titel */}
</div>


          {/* end of .top-up-details-container */}
        </div>




        {/* end of .top-up-details-main-container-full-page */}
      </div>
    )
  }


  return       ( 

    <div className="top-up-details-main-container-full-page">
      <UserBankAccountHeader />

        <form className="top-up-details-container">

          <div className="title">

            Add transaction information
            {/* end of .titel */}
          </div>


          <div className="input-line" id='name-box'>

          <input type="text" className="name" value={name} onChange={e=> inputValidator(e)} required/>
          <span className="name-placeholder-text">Name</span>


            {/* end of .input-line1 */}
          </div>


          
          <div className="input-line2" >

          <div id='dropdown' className='dropdown' 
          onClick={()=> dropdownMenu()}
          >

           <div className="option" id='option1'>
             Choose type
             </div> 

             <div className="option" onClick={(e)=> optionSelect('income', e)}>
             Income
             </div> 

            <div className="option" onClick={(e)=> optionSelect('online shopping', e)}>
              Online Shopping
            </div>

            <div className="option" onClick={(e)=> optionSelect('expenses', e)}>
              Expenses
            </div>

            <div className="option" onClick={(e)=> optionSelect('bill', e)}>
              Bill
            </div>


            <div className="option" onClick={(e)=> optionSelect('shopping', e)}>
            Shopping
            </div>

            <div className="option" onClick={(e)=> optionSelect('other person bank transfer', e)}>
            Other person bank transfer
            </div>

            <div className="option" onClick={(e)=> optionSelect('miscellaneous', e)}>
            Miscellaneous
            </div>


            {/* end of .dropdown-select */}
          </div>

          <span className="arrow-placeholder-text">
            <FiChevronDown />
          </span>

            {/* end of .input-line1 */}
          </div>


          <div className="warning-messages-holder">

           {nameVisible &&  <div className="warning-text-box">
              <RiErrorWarningLine />  &nbsp; Please enter name!
            </div>}

            {typeVisible && <div className="warning-text-box">
            <RiErrorWarningLine />  &nbsp; Please select transaction type!
            </div>}



            {/* end of .warning-messages-holder */}
          </div>


          <div className="continue-button-holder">
          
          <button id='top-up-button' type='submit' onClick={(e)=> handleSubmit(e)}>

            {showLoadingSpin && 
                        <div className="spin-loading-holder">
                        <SpinningLoadingThingy /> 
                        </div>
                        }

          
            Continue
            </button>



          {/* end of .continue-button */}
        </div>


          {/* end of .top-up-details-container */}
        </form>

        {/* end of .top-up-details-main-container-full-page */}
    </div>

)
};

export default TopUpDetails;



