import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import '../css/user-account-home-5-buttons.css'; 
import {useGlobal} from '../context';

// COMPONENTS
import AddMoneyMethod from './AddMoneyMethod';

// ICONS
import { IconContext } from "react-icons";
import {AiOutlinePieChart} from 'react-icons/ai';
import {IoCalendarClearOutline, IoDownloadOutline} from 'react-icons/io5';
import {BiPlus} from 'react-icons/bi';
import {BsArrowRightShort} from 'react-icons/bs';



const Home5ButtonsContainer = () => {

  const {addMoneyMethodVisible, setAddMoneyMethodVisible} = useGlobal(); 


  return       ( 


      <div className="home-and-add-money-bar">


        <div className='home-text-container'>
          <h2>Home</h2>
          </div>

        <div className="five-home-bar-buttons">

        {addMoneyMethodVisible && <AddMoneyMethod />}
            

          <div className="icon-holder-container">


            {/*---- -----   STATISTICS ICON     ---------------------- */}

            <Link className="icon-holder" to='/statistics'>
              <div className="actual-icon">
              <IconContext.Provider value={{ color: 'black', size: '1.75em', className: 'react-icon-class'}}>
              <div>
                <AiOutlinePieChart />
              </div>
            </IconContext.Provider>
                  
              </div>
                
                {/* end of .icon-holder */}
            </Link>

            {/*---- -----   DOWNLOAD ICON     ---------------------- */}
            <Link className="icon-holder" to='/downloads'>
              <div className="actual-icon">
              <IconContext.Provider value={{ color: 'black', size: '1.75em', className: 'react-icon-class'}}>
              <div>
                <IoDownloadOutline />
              </div>
            </IconContext.Provider>
                  
              </div>
                
                {/* end of .icon-holder */}
            </Link>


             {/*---------   SCHEDULE     ---------------------- */}

             <Link className="icon-holder" to='/schedule-transfers'>
              <div className="actual-icon">
              <IconContext.Provider value={{ color: 'black', size: '1.75em', className: 'react-icon-class'}}>
              <div>
                <IoCalendarClearOutline />
              </div>
            </IconContext.Provider>
                  
              </div>
                
                {/* end of .icon-holder */}
            </Link>



            {/* end of .icon-holder-container */}
          </div>


          {/* --------------BUTTON HOLDER FIELDS-----------  */}


          <div className="button-holder">

            <button className="home-button" onClick={()=> setAddMoneyMethodVisible(!addMoneyMethodVisible)}>

            <IconContext.Provider value={{ size: '1.75em'}}>
              <div>
              <BiPlus />
              </div>
            </IconContext.Provider>

          <span className="home-button-text">Add Money</span>

            </button>

            


            <Link className="home-button" to='/add-money'>

              <IconContext.Provider value={{ size: '1.75em'}}>
                <div>
                <BsArrowRightShort />
                </div>
              </IconContext.Provider>

              <span className="home-button-text">Send Money</span>

</Link>




            {/* end of  .button-holder */}
          </div>






            {/* end of  .five-home-bar-buttons */}
        </div>


      {/* .home-and-add-money-bar */}
      </div>

   


)
};

export default Home5ButtonsContainer;