import React, {useEffect} from 'react'; 
import {useGlobal} from '../context';
import '../css/current-balance.css';


// ICONS
import { IconContext } from "react-icons";
import { GoEye, GoEyeClosed } from 'react-icons/go';

const CurrentBalance = () => {

   const {
       userNameData, 
        currentBalanceSee, setCurrentBalanceSee,
        } = useGlobal();

   const {transactions} = userNameData; 
   

   let balance = transactions.reduce((acc,value)=> {
       let {amount} = value;
       acc = acc + parseFloat(amount);
       return acc;
   }, 0);

   let converted = new Intl.NumberFormat('eu-EU', { style: 'currency', currency: 'EUR' }).format(parseFloat(balance));


//    FUNCTIONS

const currentBalanceHide = () => {
    if(currentBalanceSee){
        setCurrentBalanceSee(false)
    } else {
        setCurrentBalanceSee(true)
    }
};





  return       ( 

    <div className="current-balance-container">

    <div className={currentBalanceSee ? 'balance-number' : 'balance-number-hidden'}>
    {converted}</div>

    <div className="hide-current-balance-container">

    <button className="see-balance-button" onClick={()=> currentBalanceHide()}>
    <IconContext.Provider value={{size: '1.2em'}}>

    {currentBalanceSee ? <GoEye /> : <GoEyeClosed />}

    </IconContext.Provider>
    {/* end of  .see-balance-button*/}
    </button>


    <span className="see-balance-button-text">
        Current Balance  
        {/* end of . see-balance-button-text*/}
    </span>


        {/* end of .hide-current-balance-container */}
    </div>





    {/* end of .current-balance-container */}
    </div>

    


)
};

export default CurrentBalance;

