import React, {} from 'react'; 
import '../css/add-money-method-2-options.css'; 
import {useGlobal} from '../context';
import {Link} from 'react-router-dom';

// ICONS
import { IconContext } from "react-icons";
import { AiOutlineCreditCard, AiOutlineUser, AiOutlineClose } from 'react-icons/ai';


const AddMoneyMethod = () => {

    const {addMoneyMethodVisible, setAddMoneyMethodVisible} = useGlobal(); 


  return       ( 

    <div className="add-money-method-container-main">


<div className="add-money-method-holder-box">

<div className="add-money-method-button" tabIndex='0' onClick={()=>setAddMoneyMethodVisible(false)}>

<IconContext.Provider value={{ color: "black", size: '1.5em' }}>

  <AiOutlineClose />

  </IconContext.Provider>

{/* end of .add-money-method-button */}
</div>





<div className="add-money-method-box-inner">


{/* ----------------------------------- ELEMENT 1 ---------- */}

<Link className="item-box" to='/top-up'>

  <div className="icon-holder">

  <IconContext.Provider value={{ color: 'black', size: '1.5em', className: 'icon-class'}}>

     <AiOutlineCreditCard />

  </IconContext.Provider>


    {/* end of .icon-holder */}
  </div>

  <div className="text-holder">

    <div className="text-holder-line1">
      Credit or Debit Card
    </div>

    <div className="text-holder-line2">
      Top Up Instantly
    </div>



    {/* end of .text-holder */}
  </div>
  
  {/* end of .item-box */}
</Link>


{/* ----------------------------------- ELEMENT 2 ------------ */}

<Link className="item-box" onClick={()=> alert('will do later')}>

  <div className="icon-holder">

  <IconContext.Provider value={{ color: 'black', size: '1.5em', className: 'icon-class'}}>

     <AiOutlineUser />

  </IconContext.Provider>


    {/* end of .icon-holder */}
  </div>

  <div className="text-holder">

    <div className="text-holder-line1">
      Request from N26 friends
    </div>

    <div className="text-holder-line2">
      Instant with MoneyBeam
    </div>



    {/* end of .text-holder */}
  </div>
  
  {/* end of .item-box */}
</Link>







{/* end of .add-money-method-box-inner */}
</div>



{/* end of .add-money-method-holder-box */}
</div>
        




        {/* end of .add-money-method-container-main */}
    </div>

)
};

export default AddMoneyMethod;